.product {
    flex: 4;
    padding: 20px;
 
}

.productTitleContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.productAddButton {
    width: 80px;
    border: none;
    padding: 5px;
    color: #fff;
    background-color: teal;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
}

.productTop {
    display: flex;
}

.productTopLeft,
.productTopRight {
    flex: 1;
}

.productTopRight {
    padding: 20px;
    margin: 20px;
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75);
}

.productInfoTop {
    display: flex;
    align-items: center;
}

.productInfoImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 20px;
}

.productName {
    font-weight: 600;
}

.productInfoBottom {
    margin-top: 10px;
}

.productInfoItem {
    width: 200px;
    display: flex;
    justify-content: space-between;
}

.productInfoKey {
    font-weight: 600;
}

.productInfoValue {
    font-weight: 100;
}

.productBottom {
    padding: 20px;
    margin: 20px;
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75);
}

.productForm {
    display: flex;
    justify-content: space-between;
}

.productFormleft {
    display: flex;
    flex-direction: column;
}

.productFormleft > label {
    margin-bottom: 10px;
    color: gray;
}
.productFormleft > input{
    margin-bottom: 10px;
    border: none;
    padding: 5px;
    border-bottom:1px solid gray ;
}
.productFormleft > input:focus{
    outline: none;
}

.productFormleft > select{
    margin-bottom: 10px;
}
.productFormRight{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.productUploader{
    display: flex;
    align-items: center;
}
.productUploaderImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    margin-right: 20px;
}
.productButton{
    border: none;
    padding: 5px;
    border-radius: 5px;
    background-color: darkblue;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
}