.widgtSm {
    flex: 1;
    padding: 20px;
    margin-right: 20px;
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, .75);

}

.widgtSmTitle {
    font-size: 22px;
    font-weight: 600;
}

.widgetSmList {
    margin: 0;
    padding: 0;
    list-style: none;
}


.widgetListItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
}


.widgetSmImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.widgetSmUser {
    display: flex;
    flex-direction: column;

}

.widgetSmUserName {
    font-weight: 600;
}

.widgetSmUserTitle {
    font-weight: 300;
}

.widgetSmButton {
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555 ;
    cursor: pointer;
}

.widgetSmIcon {
    font-size: 16px !important;
    margin-right: 5px;
}