.topbar {
    width: 100%;
    height: 50px;
    background-color: #fff;
    position: fixed;
    top: 0;
    z-index: 999;
}

.topbarWrapper {
height: 100%;
    padding: 0px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-weight: bold;
    font-size: 30px;
    cursor: pointer;
    color: darkblue;
}
.topRight{
    display: flex;
    align-items: center;
}

.topbarIconContainer{
    position: relative;
    cursor: pointer;
    color: #555;
    margin-right: 10px;
}

.topIconBadege{
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 0;
    background-color: red;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    border-radius: 50%;
}
.topAvatart{
    width: 40px;
    height: 40px;
    border-radius: 50px;
    cursor: pointer;
}